video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.month-page{
    /* background: url('C:/Users/arama/NASSCOM/nasscom/src/assests/bg.jpg') center center/cover no-repeat; */
    height: 600px;
    width: 100%;
    /* background-color: #f8efcc; */
    margin: 0%;
}

.month-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, 1fr);
    row-gap: 20px;
    column-gap: 10px;
    /* background: url('C:/Users/arama/NASSCOM/nasscom/src/assests/bg.jpg') center center/cover no-repeat; */
    margin: 40px 20px;
    
  }

  .month-left{
    background-color: transparent;
    background-size: contain;
    border-style: dotted;
    text-align: center;
  }

  .month-right{
    background-color: transparent;
    border-style: dotted;
    text-align: center;
  }
  .month-page h1 {
    display: inline-flex;
    border-bottom: 2px dotted black;
    font-weight: none;
    padding-bottom: 10px;
    padding-top: 30px;
    margin-top: 10px;
    /* font-family: 'Times New Roman', Times, serif; */
    font-style: oblique;
  }
  .month-page {
    /* margin-top: 30px; */
    height: 800px;
    /* text-align: center; */
    /* justify-content: center; */
  }

  .month-left h2{
    display: inline-flex;
    padding-top: 20px;
    border-bottom: 1px solid white;
    color: white;
  }
  .month-right h2{
    display: inline-flex;
    padding-top: 20px;
    border-bottom: 1px solid white;
    color: white;
  }
  .month-btn {
    font-size: 15px;
    padding: 15px 20px;
    margin: 12px 40px;
    border-radius: 50px;
    border: 1px solid #e6e6e6;
    background-color: #f2ad86;
    /* color: #f7cac9; */
    width: 150px;
  }
  button:hover {
    cursor: pointer;
  }

  .month-buttons{
    display: grid;
    grid-template-columns: repeat(3, 0.5fr);
    grid-template-rows: repeat(4, 1fr);
    row-gap: 10px;
    column-gap: 2px;
    /* background: url('C:/Users/arama/NASSCOM/nasscom/src/assests/bg.jpg') center center/cover no-repeat; */
    margin: 20px 20px;
    /* border: 1px solid black; */
  }

  .value{
    width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  height: 100px;
  }

  input[type=text] {
    width: 100%;
    padding: 20px 20px;
    margin: 40px 0;
    box-sizing: border-box;
    height: 250px;
  }